.top-menu {
  height: 70px;
  box-shadow: 0 3px 6px #00000029;
}
.top-menu .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.top-menu-items {
  position: absolute;
  left: 0;
  top: 95px;
  flex-direction: column;
  background-color: #FFFFFF;
  width: 100%;
  padding-bottom: 20px;
  display: none;
  box-shadow: 0 3px 6px #00000029;
}
.top-menu-items li {
  padding: 0;
}
.top-menu-items li .nav-link {
  padding: 14px 20px;
  display: block;
  border-bottom: none;
  font-family: "Open Sans", sans-serif;
  font-size: 0.6875rem;
  font-weight: 700;
  letter-spacing: 3.28px;
  color: #A5A5A5;
  text-transform: uppercase;
}
.top-menu-items li .nav-link:hover {
  color: #000000;
}

.menu-btn {
  display: none;
  position: absolute;
  opacity: 0;
  top: -10000px;
  left: -10000px;
}
.menu-btn:checked ~ .top-menu-items {
  display: flex;
}
.menu-btn:checked ~ .hamburger-menu span:nth-child(1) {
  transform: rotate(45deg) translate(2px, 5px);
}
.menu-btn:checked ~ .hamburger-menu span:nth-child(3) {
  transform: rotate(-45deg) translate(2px, -5px);
}
.menu-btn:checked ~ .hamburger-menu span:nth-child(2) {
  opacity: 0;
}

.hamburger-menu {
  display: block;
}
.hamburger-menu span {
  width: 20px;
  height: 2px;
  display: block;
  margin-bottom: 3px;
  background: #08A6E4;
  transition: 0.3s all ease-in-out;
}
.hamburger-menu span:last-child {
  margin-bottom: 0;
}

@media (min-width: 790px) {
  .top-menu-items {
    display: flex;
    flex-direction: row;
    position: relative;
    top: 0;
    box-shadow: none;
    padding-bottom: 0;
  }
  .top-menu-items li {
    padding: 0 26px;
  }
  .top-menu-items li .nav-link {
    padding: 27px 18px;
    border-bottom: 4px solid transparent;
  }
  .top-menu-items li .nav-link:hover {
    border-color: #08A6E4;
  }
  .top-menu-items li:last-child {
    padding-right: 0;
  }

  .hamburger-menu {
    display: none;
  }
}
