  width: 100%;
}
.contact .container .contact-info p {
  margin: 5px 0 35px;
}

.contact-tools {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  margin-bottom: 53px;
}
.contact-tools .e-mail,
.contact-tools .telephone {
  font-family: "Open Sans", sans-serif;
  color: #A5A5A5;
  font-weight: 500;
  margin-bottom: 10px;
}
.contact-tools .e-mail::before,
.contact-tools .telephone::before {
  content: "";
  width: 24px;
  height: 24px;
  display: inline-block;
  margin-right: 10px;
  position: relative;
  top: 7px;
}
.contact-tools .e-mail::before {
  background: url("../assets/Mail Icon.svg");
}
.contact-tools .telephone::before {
  background: url("../assets/Phone Icon.svg");
}

.form {
  display: flex;
  flex-direction: column;
}
.form .user-info {
  display: flex;
  flex-direction: column;
}
.form .user-info input {
  border: none;
  border-bottom: 1px solid #A5A5A5;
  line-height: 2.625rem;
}
.form .user-info input[type=text] {
  margin-bottom: 40px;
}
.form .user-info .user-name,
.form .user-info .user-email {
  text-transform: uppercase;
  font-size: 0.6875rem;
  letter-spacing: 3.28px;
  font-family: "Open Sans", sans-serif;
  color: #08A6E4;
  font-weight: 700;
}
.form .user-agree {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.form .user-agree .checkbox {
  position: relative;
  display: block;
}
.form .user-agree .checkbox input {
  opacity: 0;
  height: 0;
  width: 0;
  position: absolute;
}
.form .user-agree .checkbox p {
  margin-left: 35px;
  font-size: 0.75rem;
  color: #A5A5A5;
  font-family: "Open Sans", sans-serif;
}
.form .user-agree .checkbox span {
  position: absolute;
  height: 24px;
  width: 24px;
  border: 1px solid #D1D1D1;
  border-radius: 4px;
  cursor: pointer;
}
.form .user-agree .checkbox input:checked + span {
  background: url("../assets/Checkmark.svg") no-repeat;
  background-position: center;
}
.form .medium-btn {
  background: #08A6E4;
  font-size: 0.8125rem;
  color: #FFFFFF;
  border-radius: 35px;
  padding: 20px 97px;
  margin-top: 20px;
  align-self: flex-end;
  border: none;
}

@media (min-width: 1024px) {
  .contact .container {
    margin: 155px auto;
    flex-direction: row;
    justify-content: space-between;
  }
  .contact .container .contact-info,
.contact .container .contact-form {
    width: 50%;
  }
  .contact .container .contact-info p {
    margin: 22px 0 42px;
  }

  .contact-tools {
    flex-direction: row;
    justify-content: flex-start;
    margin-bottom: 0;
  }
  .contact-tools .e-mail,
.contact-tools .telephone {
    margin-right: 20px;
  }
}
.container-footer {
  margin-top: 59px;
  box-shadow: 0px -5px 40px #00000029;
}
.container-footer .container {
  height: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.container-footer .container .logo-footer {
  font-size: 1.625rem;
  font-weight: 300;
  font-family: "Raleway", sans-serif;
  color: #08A6E4;
}
.container-footer .container .copyrights {
  font-size: 0.6875rem;
  color: #A5A5A5;
  font-family: "Open Sans", sans-serif;
  margin-top: 6.5px;
}
.container-footer .container .social-media a img {
  margin-left: 20px;
}